import vNodata from "@/components/v-nodata";
export default {
  data() {
    return {
      noInfo: false,
      goldDetailData: []
    };
  },
  components: {
    "v-nodata": vNodata
  },
  beforeCreate() {
    document.title = "提现明细";
  },
  created() {
    // 从缓存中拿明细
    const goldDetail = window.localStorage.getItem('goldDetail');
    if (!!goldDetail) {
      this.goldDetailData = JSON.parse(goldDetail);
    } else {
      this.noInfo = true;
    }
  }
};



